import styles from './OrderDetails.module.css';
import orderDoneImg from '../../images/order_done.png';
import { useSelector } from '../../hooks';
import { FC } from 'react';

export const OrderDetails: FC = () =>
{
    const orderId = useSelector(store => store.order.orderId);

    let content;
    if (orderId)
        content = (
            <>
                <div className={`${styles.orderId} text text_type_digits-large pt-30`}>
                    <span>{orderId}</span>
                </div>
                <div className="text text_type_main-medium mt-8"> </div>
                <img className="mt-15" src={orderDoneImg} alt=" " />
                <div className="text text_type_main-default mt-15">   </div>
                <div className="text text_type_main-default text_color_inactive mt-2 mb-30">    </div>
            </>);
    else
        content = (
            <>
                <div className={`text text_type_main-large ${styles.forming}`}> </div>
                <div className={`text text_type_main-medium ${styles.forming}`}>...</div>
            </>
        )

    return (
        <div className={`${styles.orderDetail} modal-ontent`}>
            {content}
        </div>
    );
}